<template>
  <el-main class="main" v-loading="pageLoading" element-loading-text="邮件发送中...">
    <el-row type="flex" class="row-bg" justify="center" style="width:100%">
      <el-col class="alert" :xs="22" :sm="20" :md="12" :lg="10" :xl="8">
        <el-alert
          title="此处并不会验证账号是否存在"
          type="warning"
          center
          show-icon>
        </el-alert>
      </el-col>
    </el-row>
    <el-row type="flex" class="row-bg" justify="center" style="width:100%">

    <el-card class="box-card" shadow="hover">
      <div slot="header" class="clearfix">
        <span>激活邮件重发</span>
      </div>
      <el-form :model="dynamicValidateForm" ref="dynamicValidateForm" label-width="100px" class="demo-dynamic" @submit.native.prevent>
        <el-form-item
          prop="email"
          label="邮箱"
          :rules="[
            { required: true, message: '请输入邮箱地址', trigger: 'blur' },
            { type: 'email', message: '请输入正确的邮箱地址', trigger: ['blur', 'change'] }
          ]"
        >
          <el-input v-model="dynamicValidateForm.email" @keyup.enter.native.stop="submitForm" placeholder="您的社区注册邮箱" clearable></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" :loading="pageLoading" @click="submitForm">提交</el-button>
        </el-form-item>
      </el-form>
    </el-card>
    </el-row>
  </el-main>
</template>

<script>
  import axios from 'axios'

  export default {
    name: "UserEmailAgain",
    data(){
      return{
        pageLoading: false,
        dynamicValidateForm:{
          email: '',
        }
      }
    },
    methods:{
      submitForm(){
        this.$refs['dynamicValidateForm'].validate((valid) => {
          if (valid) {
            this.pageLoading = true;
            let data = new FormData();
            data.append('email',this.dynamicValidateForm.email);

            axios.post('/email/again', data)
            .then(resp=>{
              console.log(resp)
              if (resp.data.status === 200) {
                this.$notify.success(resp.data.message)
              }else {
                this.$notify.error(resp.data.message)
              }
            })
            .catch(()=>{
              this.$notify.error('请求错误')
            })
            .finally(()=>{
              this.pageLoading = false;
            })
          }
        });
      }
    }
  }
</script>

<style scoped>
  .clearfix:after {
    clear: both
  }

  .box-card {
    width: 480px;
  }

  .card-body{
    padding-right: 80px;
  }

  .alert{
    margin: 60px auto;
  }
</style>
